#preloader {
  position: relative;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  margin-left: -26px;
}

#preloader span {
  display: block;
  bottom: 0px;
  width: 9px;
  height: 5px;
  background: #9b59b6;
  position: absolute;
  animation: preloader 1.5s infinite ease-in-out;
  z-index: 100;
}

#preloader span:nth-child(2) {
  left: 11px;
  animation-delay: 0.2s;
}
#preloader span:nth-child(3) {
  left: 22px;
  animation-delay: 0.4s;
}
#preloader span:nth-child(4) {
  left: 33px;
  animation-delay: 0.6s;
}
#preloader span:nth-child(5) {
  left: 44px;
  animation-delay: 0.8s;
}
@keyframes preloader {
  0% {
    height: 5px;
    transform: translateY(0px);
    background: #9b59b6;
  }
  25% {
    height: 30px;
    transform: translateY(15px);
    background: #dc56c5;
  }
  50% {
    height: 5px;
    transform: translateY(0px);
    background: #9b59b6;
  }
  100% {
    height: 5px;
    transform: translateY(0px);
    background: #9b59b6;
  }
}

.bg-preloader {
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #212121c5;
  position: fixed;
}
